.color-picker-container {
    /* border: 1px solid; */
    background: white;
    border-radius: 10px;
    padding-top: 10px;
    display: flex;
    width: 244px;
    flex-direction: column;
  }
  
  .additional-division {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
  }
  
  .additional-option {
    flex: 1;
    text-align: center;
    cursor: pointer;
  }

  .picker-container {
    display: flex;
    justify-content: space-between;
  }
  
  .picker-option {
    flex: 1;
    text-align: 'center';
    color: 'black';
    cursor: 'pointer';
    position: relative;
  }
  